@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
*{margin:0;padding:0;border:none;outline:none;font-family:"Inter", sans-serif;color:#333333}body{background-color:var(--bg);position:relative;height:100%;overflow:hidden;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body *{box-sizing:border-box}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus input:-webkit-autofill,textarea:-webkit-autofill,textarea:-webkit-autofill:hover textarea:-webkit-autofill:focus,select:-webkit-autofill,select:-webkit-autofill:hover,select:-webkit-autofill:focus{border:0;box-shadow:0 0 0px 1000px transparent inset;-webkit-transition:background-color 5000s ease-in-out 0s;transition:background-color 5000s ease-in-out 0s;background:-webkit-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(0,174,255,0.04) 50%, rgba(255,255,255,0) 51%, rgba(0,174,255,0.03) 100%)}.base-input{width:100%;padding:10px;background-color:#eae8e9;border:2px solid #c4c2c4;border-radius:8px}.base-input--title{margin-bottom:24px}.base-input input{width:100%;height:100%;font-size:16px;background-color:inherit}.base-todo-input{margin-top:32px}.base-todo-input button{border-radius:8px;color:#f5f5f4;background-color:#3454d1;cursor:pointer;width:100px;font-size:14px;padding:8px;margin-top:16px}.base-todo-input button:hover{background-color:#657cda}.base-animation{-webkit-animation:0.4s base;animation:0.4s base}@-webkit-keyframes base{0%{opacity:0}100%{opacity:1}}@keyframes base{0%{opacity:0}100%{opacity:1}}

.container{width:100%;height:100vh;display:-webkit-flex;display:flex}.container .page{-webkit-flex:1 1;flex:1 1;padding:20px}

.nav{width:15%;min-width:200px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:20px}.nav__item{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:100%;padding:12px 28px;border-radius:8px;text-decoration:none;color:#333333;font-size:16px}.nav__item:first-child{margin-bottom:4px}.nav__item:hover{border:2px solid #F9EDD2;padding:10px 26px}.nav__item--active{background-color:#f8e5bb}.nav__item-title{margin-left:16px}.nav__item-icon{width:24px;height:24px}.nav__item-icon svg{width:100%;height:100%}

.edit{width:100%;height:100%;-webkit-flex-direction:row;flex-direction:row}.edit--empty{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.edit__empty{font-size:24px;text-align:center;color:#818181}.edit__empty:hover{opacity:0.7}.edit__content{width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.edit__content .keep{min-width:30%;height:500px}.edit__content .keep .todo{margin-bottom:12px}.edit__form{margin-left:48px;width:20%}.edit__buttons{margin-top:48px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.edit__btn{padding:15px;border-radius:8px;cursor:pointer;font-size:16px}.edit__btn:not(:last-child){margin-bottom:16px}.edit__btn:hover{box-shadow:0px 4px 8px 0px rgba(0,0,0,0.2)}.edit__save,.edit__delete,.edit__close{color:#f5f5f4}.edit__save{background-color:#28a745}.edit__save:hover{background-color:#218838}.edit__decline{background-color:#f8f9fa}.edit__decline:hover{background-color:#e2e6ea}.edit__delete{background-color:#dc3545}.edit__delete:hover{background-color:#c82333}.edit__close{background-color:#007bff}.edit__close:hover{background-color:#007bff}

.keep{min-width:300px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;max-width:30%;min-height:200px;padding:20px;border-radius:16px;background-color:#f5f5f4;box-shadow:0px 6px 12px -6px rgba(18,20,23,0.12),0px 8px 24px -4px rgba(18,20,23,0.08);border:1px solid #eae8e9;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.keep:hover{cursor:pointer;border:1px solid #979797;background-color:#dfdfdf;box-shadow:0px 12px 24px -6px rgba(18,20,23,0.3),0px 16px 48px -4px rgba(18,20,23,0.2)}.keep--edit .keep__header h3{width:100%}.keep--edit:hover{cursor:unset;border:1px solid #eae8e9;background-color:#f5f5f4;box-shadow:0px 6px 12px -6px rgba(18,20,23,0.12),0px 8px 24px -4px rgba(18,20,23,0.08)}.keep__header{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:16px}.keep__header h3{width:80%;max-height:100px;overflow:hidden}.keep__todos-container{width:100%;height:100%}.keep__todos{height:100%;width:100%;padding-right:24px}.keep .todo:not(:last-child){margin-bottom:8px}.keep__btns{display:-webkit-flex;display:flex}.keep__delete,.keep__edit{width:24px;height:24px;padding:4px;border-radius:8px}.keep__delete svg,.keep__edit svg{width:100%;height:100%}.keep__delete:hover{padding:3px;border:1px solid #f34213}.keep__edit{margin-right:4px}.keep__edit:hover{padding:3px;border:1px solid #3454d1}

.todo{width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.todo__checkbox,.todo__delete{width:16px;height:16px;cursor:pointer}.todo__checkbox svg,.todo__delete svg{width:100%;height:100%}.todo__checkbox:hover,.todo__delete:hover{opacity:0.5}.todo__checkbox{margin-right:12px}.todo__delete{margin-left:12px}.todo__content,.todo__content--checked{-webkit-flex:1 1;flex:1 1;font-size:16px;overflow:hidden;white-space:nowrap}.todo__content--checked{text-decoration:line-through;color:#6f6f72}.todo__content--input{background:transparent}

.confirm-modal__buttons{margin-top:16px;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;width:100%}.confirm-modal__confirm,.confirm-modal__decline{width:45%;padding:10px;border-radius:8px;font-size:16px;cursor:pointer}.confirm-modal__confirm:hover,.confirm-modal__decline:hover{padding:8px}.confirm-modal__confirm{background-color:#ff4747;color:#f5f5f4}.confirm-modal__confirm:hover{background-color:#ff4747c9;border:2px solid #831b1b}.confirm-modal__decline{background-color:#dadada}.confirm-modal__decline:hover{background-color:#dadadac5;border:2px solid #9c9c9c}

.main{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.main__create-keep{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:10px 25px;border-radius:16px;background-color:#3454d1}.main__create-keep span{font-size:22px;color:#f5f5f4;margin-left:16px}.main__create-keep__icon{width:42px;height:42px}.main__create-keep:hover{cursor:pointer;background-color:#657cda}.main__content{margin-top:20px;width:100%;height:100%}.main__keeps-container{width:100%;height:100%}.main__keeps{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-gap:20px 20px;gap:20px 20px;padding-bottom:10px}.main__keeps-placeholder{width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;color:#707070}

.create-modal__body{width:30%;min-height:400px;max-height:800px}.create-modal__form{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.create-modal .column{width:45%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.create-modal__submit{border-radius:8px;color:#f5f5f4;background-color:#3454d1;cursor:pointer}.create-modal__submit:hover{background-color:#657cda}.create-modal .todo-list{margin-bottom:24px}.create-modal .todo-list h3{margin-bottom:16px}.create-modal .todo-list .todo:not(:last-child){margin-bottom:8px}.create-modal__submit{margin-top:auto;padding:10px;font-size:16px}

.base-modal{position:absolute;z-index:999;top:0;left:0;width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background-color:rgba(0,0,0,0.5)}.base-modal__body{overflow-y:auto;padding:24px;border-radius:16px;background:#f5f5f4;box-shadow:0 32px 70px rgba(33,33,33,0.3);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.base-modal__header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:24px}.base-modal__close{width:24px;height:24px;cursor:pointer}.base-modal__close svg{width:100%;height:100%}.base-modal__close:hover{opacity:0.5}

